/**
 * PromoCode.js
 * PromoCode field that takes in a callback
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Input } from 'reactstrap';

import Button from 'components/FormsV3/Button/Button';
import FormInputWrapper from '../FormInputWrapper/FormInputWrapper';

import '../TextInput/TextInput.scss';
import './EmailInput.scss';


/**
 * Custom Form Dropdown class
 */
class EmailInput extends Component {
  render() {
    const {
      name,
      desc,
      placeholderString,
      extraInfo,
      errorString,
      onClick,
      buttonText,
      onChange,
      value,
    } = this.props;

    return (
      <FormInputWrapper
        desc={desc}
        extraInfo={extraInfo}
        errorString={errorString}
        className="email-input-container"
      >
        <div className="email-input-inner-container">
          <div className="text-input">
            <Input
              placeholder={placeholderString}
              name={name}
              ref={node => this.input = node}
              onChange={e => onChange('email', name, e.target.value)}
              value={value}
            />
            <div className="promo-button d-none d-sm-block">
              <Button
                variant="medium-flat-green"
                height={48}
                width={130}
                onClick={() => {
                  onClick(value);
                }}
              >
                <p>{buttonText}</p>
              </Button>
            </div>
          </div>
        </div>
        <div className="promo-button d-flex d-sm-none">
          <Button
            variant="medium-flat-green"
            height={48}
            width={130}
            onClick={() => {
              onClick(value);
            }}
          >
            <p>{buttonText}</p>
          </Button>
        </div>
      </FormInputWrapper>
    );
  }
}

EmailInput.defaultProps = {
  desc: null,
  extraInfo: null,
  errorString: null,
};


EmailInput.propTypes = {
  placeholderString: PropTypes.string.isRequired,
  name: PropTypes.string.isRequired,
  onClick: PropTypes.func.isRequired,
  desc: PropTypes.string,
  extraInfo: PropTypes.object,
  errorString: PropTypes.string,
  buttonText: PropTypes.string.isRequired,
  onChange: PropTypes.func.isRequired,
  value: PropTypes.string.isRequired,
};

export default EmailInput;
