import { withRouter } from 'react-router-dom';
import React, { Component } from 'react';
import { Row } from 'reactstrap';
import PropTypes from 'prop-types';

import MainInfo from 'components/Business/BusinessLanding/MainInfo/MainInfo';

import './LandingBusiness.scss';

class LandingBusiness extends Component {
  render() {
    const { history } = this.props;

    return (
      <div className="landing-business-container business-main-container w-100 my-5">
        <Row className="w-100 mx-auto" noGutters>
          <MainInfo
            isFromPersonal
            goToBusinessLanding={() => history.push('/business')}
            showEnquiryModal={() => {}}
          />
        </Row>
      </div>
    );
  }
}

LandingBusiness.defaultProps = {
};

LandingBusiness.propTypes = {
  history: PropTypes.object.isRequired,
};

export default withRouter(LandingBusiness);
