/**
 * FormInputWrapper.js
 * Custom form dropdown menu with input
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import React, { Component } from 'react';
import PropTypes from 'prop-types';

import './FormInputWrapper.scss';

/**
 * Custom Form Dropdown class
 */
class FormInputWrapper extends Component {
  render() {
    const {
      extraInfo,
      desc,
      errorString,
      smallMargin,
      className,
      isDateRange,
    } = this.props;

    return (
      <div
        className={['input-wrapper-main-v3 input-wrapper-error', smallMargin && 'my-1', className].join(' ')}
      >
        {
          // eslint-disable-next-line
          isDateRange ?
            <div className="d-flex">
              <p className="input-wrapper-desc w-50">Start Date</p>
              <p className="input-wrapper-desc w-50">End Date</p>
            </div>
          :
          desc !== null ?
            <p className="input-wrapper-desc">{desc}</p>
          :
            null
        }
        {this.props.children}
        {
          extraInfo !== null ?
            <div className="extra-info-container">
              {extraInfo}
            </div>
          :
            null
        }
        {
          errorString !== null ?
            <div className="input-wrapper-error-text">
              <p>{errorString}</p>
            </div>
          :
            null
        }
      </div>
    );
  }
}

FormInputWrapper.defaultProps = {
  desc: null,
  extraInfo: null,
  errorString: null,
  smallMargin: false,
  className: '',
  isDateRange: false,
};


FormInputWrapper.propTypes = {
  desc: PropTypes.string,
  extraInfo: PropTypes.object,
  errorString: PropTypes.string,
  smallMargin: PropTypes.bool,
  className: PropTypes.string,
  isDateRange: PropTypes.bool,
};

export default FormInputWrapper;
